.experience {
    padding: 50px;
    padding-right: 100px;
}

.title {
    font-size: 30px;
    font-weight: 600;
}

.title:hover {
    color: var(--color-6);
}

.informations {
    margin-top: 25px;
}

.informationItem {
    border: 1px solid var(--color-3);
    padding: 20px 50px;
    position: relative;
    width: calc(100% - 50px);
    margin-bottom: 30px;
}

.informationItem::before, .informationItem::after, .informationItem div::before, .informationItem div::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    border: 4px solid var(--color-6);
}

.informationItem::before {
    top: -5px;
    left: -5px;
    border-right: none;
    border-bottom: none;
    transition: top 0.1s ease-in, left 0.1s ease-out;
}

.informationItem:hover::before {
    top: -8px;
    left: -8px;
}

.informationItem::after {
    top: -5px;
    right: -5px;
    border-left: none;
    border-bottom: none;
    transition: top 0.1s ease-in, right 0.1s ease-out;
}

.informationItem:hover::after {
    top: -8px;
    right: -8px;
}

.informationItem div::before {
    bottom: -5px;
    left: -5px;
    border-right: none;
    border-top: none;
    transition: bottom 0.1s ease-in, left 0.1s ease-out;
}

.informationItem:hover div::before {
    bottom: -8px;
    left: -8px;
}

.informationItem div::after {
    bottom: -5px;
    right: -5px;
    border-left: none;
    border-top: none;
    transition: bottom 0.1s ease-in, right 0.1s ease-out;
}

.informationItem:hover div::after {
    bottom: -8px;
    right: -8px;
}

.role {
    font-size: 18px;
    font-weight: 600;
}

.role small {
    font-weight: normal;
}

.period {
    float: right;
}

.skillBoxWrapper {
    display: flex;
    flex-wrap: wrap;
}

.skillBox {
    padding: 10px;
}

.skillBox + .skillBox {
    margin-left: 10px;
}